*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: 0;
}

.users {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 20px 10px;
}

.users button {
  display: block;
  border: 1px solid #000;
  background: #fff;
  width: 200px;
  padding: 10px 20px;
  margin-bottom: 40px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}
.users button:hover {
  background: #000;
  color: #fff;
}
.single-user-container {
  flex-basis: 100%;
  flex: 1;
  display: flex;
  justify-content: center;
}

.single-user {
  display: flex;
  padding: 25px;
  width: 290px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px solid #000;
  margin-bottom: 20px;
  position: relative;
}

.single-user img {
  width: 150px;
  border-radius: 50%;
  margin-bottom: 15px;
}

.user small {
  display: block;
  font-size: 10px;
}
.single-user .remove {
  width: 50px;
  height: 40px;
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.single-user .remove::after {
  content: '\2716';
}
